import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import { Ukrainian } from 'flatpickr/dist/l10n/uk.js';
import imgErrorIcon from '../img/bi_x-octagon.png';
const btn = document.querySelector('button');
const input = document.querySelector('#datetime-picker');
const spanD = document.querySelector('[data-days]');
const spanH = document.querySelector('[data-hours]');
const spanM = document.querySelector('[data-minutes]');
const spanS = document.querySelector('[data-seconds]');

btn.disabled = true;
let userSelectedDate = null;

const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  locale: Ukrainian,
  onClose(selectedDates) {
    const selected = selectedDates[0];
    console.log(selected);
    if (selected.getTime() < Date.now()) {
      btn.disabled = true;
      iziToast.error({
        iconUrl: imgErrorIcon,
        class: 'box',
        title: 'Error',
        message: 'Please choose a date in the future',
        position: 'topCenter',
        messageColor: 'white',
        messageSize: '16px',
        backgroundColor: '#ef4040',
        titleColor: '#fff',
      });
      return;
    }
    btn.disabled = false;
    userSelectedDate = selected;
  },
};
let interval = 0;
btn.addEventListener('click', () => {
  btn.disabled = true;
  input.disabled = true;
  interval = setInterval(() => {
    const timeLeft = userSelectedDate.getTime() - Date.now();
    if (timeLeft <= 0) {
      input.disabled = false;
      clearInterval(interval);
      return;
    }
    const { days, hours, minutes, seconds } = convertMs(timeLeft);

    spanD.textContent = addLeadingZero(days);
    spanH.textContent = addLeadingZero(hours);
    spanM.textContent = addLeadingZero(minutes);
    spanS.textContent = addLeadingZero(seconds);
  }, 1000);
});
function addLeadingZero(value) {
  return value.toString().padStart(2, '0');
}
flatpickr('#datetime-picker', options);
function convertMs(ms) {
  // Number of milliseconds per unit of time
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  // Remaining days
  const days = Math.floor(ms / day);
  // Remaining hours
  const hours = Math.floor((ms % day) / hour);
  // Remaining minutes
  const minutes = Math.floor(((ms % day) % hour) / minute);
  // Remaining seconds
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);

  return { days, hours, minutes, seconds };
}

console.log(convertMs(2000)); // {days: 0, hours: 0, minutes: 0, seconds: 2}
console.log(convertMs(140000)); // {days: 0, hours: 0, minutes: 2, seconds: 20}
console.log(convertMs(24140000)); // {days: 0, hours: 6 minutes: 42, seconds: 20}
